import * as React from 'react';
import Button from '@mui/material/Button';
import { Outlet, Link } from "react-router-dom";
interface NavProps {
    
}
 
interface NavState {
    
}
 
class Nav extends React.Component<NavProps, NavState> {
    state = {hello : "hello" }
    render() { 
        return ( 
            <div className='jankioHeader'>
                
                <Link to="/"><img alt="jankio" className="logo" src="jankio.png" /></Link>

                <nav className="nav">
                    <Button variant="contained"><Link to="/">Feed</Link></Button>
                    <Button variant="contained"><Link to="/compose">Compose</Link></Button>
                    <Button variant="contained"><Link to="/profile">Profile</Link></Button>
                    <Button variant="contained"><Link to="/settings">Settings</Link></Button>
                </nav>

                <Outlet />
            </div>
         );
    }
}
 
export default Nav;