import { useCallback } from 'react';
import { Handle, Position } from 'reactflow';

function TextBlock() {

  const jodeStyle = {
    width: "100%"
  }

  const inputStyle = {
    width: "100%"
  }

  const styleLabel = {
    display: "block",
    color: "#777",
    fontSize: "12px"
  }

  const onChange = useCallback((evt: any) => {
    console.log(evt.target.value);
  }, []);

  return (
    <>
      <Handle type="source" position={Position.Right} />
      <div style={jodeStyle}>
        <label style={styleLabel} htmlFor="text">Output:</label>
        <input style={inputStyle} id="text" name="textBlock" onChange={onChange} />
      </div>
    </>
  );
}

export default TextBlock;