import * as React from 'react';
import Post from '../components/Post';
import axios from 'axios';
import { Component } from 'react';
import { IPost } from '../models/post.model';

type FeedState = {
  data: Array<IPost>
}
export default class Feeds extends Component<{}, FeedState> {
  constructor(props: any) {
    super(props)

    this.state = {
      data: new Array<IPost>()
    }
  }

  getData() {
    axios.get('./posts.json').then(res => {
      var data = res.data
      this.setState({ data: data })
    })
  }

  componentDidMount() {
    this.getData()
  }

  render() {
    return (
      <>
      <div className="griddy">
        <div className="feed">
          {this.state.data.map((post: IPost, i: number) => <Post key={i} post={post}></Post>)}
        </div>
      </div>
      </>
    )
  }
}
// export default Feed;
