import { useCallback } from 'react';
import { Handle, Position } from 'reactflow';

const handleStyle = { left: 10 };

function Output() {

  const jodeStyle = {
    width: "100%"
  }

  const inputStyle = {
    width: "100%"
  }

  const styleLabel = {
    display: "block",
    color: "#777",
    fontSize: "12px"
  }

  const onChange = useCallback((evt: any) => {
    console.log(evt.target.value);
  }, []);

  return (
    <>
      <Handle type="target" position={Position.Left} />
      <div style={jodeStyle}>
        <label style={styleLabel} htmlFor="text">Output:</label>
        <input style={inputStyle} id="text" name="text" onChange={onChange} />
      </div>
    </>
  );
}

export default Output;