import React, { useState } from "react";
import Feed from "./Feed";
import ProfileFeed from "./ProfileFeed";

interface ProfileProps {
    
}
 
interface ProfileState {
    
}

const pfpStyle = {
        width: '150px',
        borderRadius: '50%',
        border: '4px solid #a2e4b8'
    } 

const bioStyle = {
    backgroundImage: "url('profilepic2.png')",
    backgroundSize: 'cover',
    margin: '16px',
    borderRadius: '3px'
}

     
class Profile extends React.Component<ProfileProps, ProfileState> {
    state = { Profile: "profile" }

    render() { 
        return ( 
            <>
                <div className="profile">
                    <div style={bioStyle} className="bio">

                        <img style={pfpStyle} src="profilepic3.png" />

                        <h2 className="name">umbrellaman</h2>

                        <h4 className="biojank">This is a bio for umbrellaman. Maybe we can allow each user to create a jank AS their bio, we'll see.</h4>

                    </div>

                    <div>
                        <ProfileFeed></ProfileFeed>
                    </div>

                </div>
            </>
         );
    }
}
 
export default Profile;