import { Button } from '@mui/material';
import { useCallback, useMemo } from 'react';
import ReactFlow, {
  MiniMap,
  Controls,
  Background,
  useNodesState,
  useEdgesState,
  addEdge,
} from 'reactflow';
//  you need to import the reactflow styles
import 'reactflow/dist/style.css';
// jode imports
import Output from '../jodes/output.jode';
import TextBlock from '../jodes/textBlock.jode'


const initialNodes = [
  { id: '1', type: 'textBlock', position: { x: 0, y: 0 }, data: { label: 'Text Block' } },
  { id: '2', type: 'textBlock', position: { x: 0, y: 100 }, data: { label: 'Text Block' } },
  { id: '3', type: 'output', position: { x: 250, y: 50 }, data: { label: 'Output' } },
];

const initialEdges = [
    { id: 'e1-2', source: '1', target: '3', animated: true, type: 'smoothstep' },
    { id: 'e2-3', source: '2', target: '3', animated: true, type: 'smoothstep' },];

function Compose() {
  const [nodes, setNodes, onNodesChange] = useNodesState(initialNodes);
  const [edges, setEdges, onEdgesChange] = useEdgesState(initialEdges);

  const onConnect = useCallback((params: any) => setEdges((eds) => addEdge(params, eds)), [setEdges]);
  const nodeTypes = useMemo(() => ({ output: Output, textBlock: TextBlock }), []);

  return (
    <div className="composer"> 
        <ReactFlow
        nodeTypes={nodeTypes}
        nodes={nodes}
        edges={edges}
        onNodesChange={onNodesChange}
        onEdgesChange={onEdgesChange}
        onConnect={onConnect}
        fitView
        >
        <MiniMap />
        <Controls />
        <Background />
        </ReactFlow>

        <div className="postBtn">
            <Button className="postBtn" variant="contained">post this jank</Button>
        </div>
    </div>
  );
}

export default Compose;