import * as React from 'react';
import { Component } from 'react';
import { IPost } from '../models/post.model';


interface PostProps {
    post: IPost
}
 
interface PostState {
    
}
 
export default class Post extends Component<PostProps, PostState> {
    state = { Post: "post" }
    render() { 
        return ( 
            <div className="post">
                <h2>{this.props.post.userAlias}</h2>
                <h3>{this.props.post.jankData.body}</h3>
                <h4>{"Posted on: " + this.props.post.date}</h4>
                <h5>52 Janklies</h5>
            </div>
         );
    }
}
 
